:root {
  --primary: #2B3B73;
  --secondary: #43A58B;
  --info: #B0CB14;
  --text: #0C0B0B;
  --text-90: rgba(12, 11, 11, 0.9);
  --gray-1: #212529;
  --gray-2: #495057;
  --gray-3: #9DA2AB;
  --success: #0A1B28;
  --warning: #050F17;
  --card: #FAFAFA;
  --card-1: #E9ECEF;
  --card-2: #F8F9FA;
  --line: #EEEEEE;
  --white: #FFFFFF;
  --black: #0C0B0B;
  --background: #FFFFFF;
  --card-background: #FAFAFA;
  --error: #dc3545;
  --gray: #999999;
}

:root {
  --font-family: Antenna, Arial, sans-serif;
  --font-base: Antenna;
  --font-fallback: Arial, sans-serif;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-size-h1: 3rem;
  --font-size-h2: 2.625rem;
  --font-size-h3: 2.25rem;
  --font-size-h4: 1.5rem;
  --font-size-h5: 1.375rem;
  --font-size-h6: 1.25rem;
  --font-size-title: 1.5rem;
  --font-size-title1: 1.375rem;
  --font-size-body: 1.125rem;
  --font-size-body1: 1rem;
  --font-size-body2: 0.875rem;
  --font-size-body3: 0.75rem;
  --font-size-body4: 0.625rem;
  --line-height-tight: 1;
  --line-height-normal: 1;
  --line-height-relaxed: 1.4;
}

